@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&family=Poppins:wght@200;400;600;700&display=swap');

/* COMMON STYLES */

:root {
  /* main colors */
  --first-color: #0a1010;
  --second-color: #87518F;
  --third-color: #30B2B0;
  --fourth-color: #FFB1A5;
  --fifth-color: #346474;
  --background-color: #F1F1EF;

  /* gradients */
  --gradient-1: linear-gradient(135deg, var(--second-color) 0,var(--fifth-color) 51%,var(--fourth-color));
  --gradient-2: linear-gradient(180deg, var(--background-color) 65%,var(--fifth-color) ,var(--second-color));
  
  /* scroll bar colors */
  --scroll-bar-color: #f5eee8;
  --scroll-thumb-color: #dfd2d9;
  --scroll-thumb-hover-color: #87518F;
  
   /* box shadow */
    --box-shadow: 0px 5px 8px rgb(0 0 0 / 20%);
    --box-shadow-2: 0px 0px 20px rgb(0 0 0 / 15%);

}


*{
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
}

html {
    scroll-behavior: smooth;
}

body {
    background-image: var(--gradient-2);
    margin: 0;
    transition: .5s ease;
    color: var(--first-color);
    height: 100vh;
}

a {
  text-decoration: none;
}

li{
  list-style: none;
}

/* Scroll Bar */
::-webkit-scrollbar{
  width: 6px;
  background: var(--scroll-bar-color);
}

::-webkit-scrollbar-thumb{
  background: var(--scroll-thumb-color);
  border-radius: 2em;
}

::-webkit-scrollbar-thumb:hover{
  background: var(--scroll-thumb-hover-color);
}

/* BACKGROUND */
@keyframes move {
  100% {
      transform: translate3d(0, 0, 1px) rotate(360deg);
  }
}

.background {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: var(--background-color);
  overflow: hidden;
}

.background span {
  width: 39vmin;
  height: 39vmin;
  border-radius: 39vmin;
  backface-visibility: hidden;
  position: absolute;
  animation: move;
  animation-duration: 45;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}


