/* PAGE */
.page {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 20px 15px;
  height: 100vh;
  z-index: 111111;
}

/* HEADER */

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  align-items: center;
  margin-bottom: 10px;
  margin-top: 25px;
  z-index: 111111;

}

.header-avatar {
  height: auto;
  width: 125px;
  margin: auto;
  z-index: 111111;

}

.header-title{
  color: var(--first-color);
  font-size: 26px;
  margin-top: -8px;
  z-index: 111111;
}

.header-description {
  font-size: 14px;
  margin-top: -5px;
  text-align: center;
  z-index: 111111;
}

/* SOCIAL MEDIA */

.socials{
  display: flex;
  gap: 15px;
  justify-content: center;
  align-items: center;
  z-index: 111111;

}

.socials >a {
  color: var(--second-color);
  font-size: 24px;
}

/* LINKS */

.links {
  display: block;
}

.links-content {
  margin: 15px 0 50px 0;
  z-index: 111111;
}

.link {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-block: 22px;
  background-color: var(--first-color);
  border-radius: 14px;
  cursor: pointer;
  box-shadow: var(--box-shadow);
}

.link:hover{
  background-image: var(--gradient-1);
}

.link > a{
  display: inline-block;
  color: var(--background-color);
  padding: 5px 12px;
}

.link a > i {
  margin-right: 8px;
  color: #c993d1;
}

.link:hover > a > i{
  color: var(--background-color);

}

/* FOOTER */
.footer{
    z-index: 111111;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    font-size: 10px;
    margin: 15px;
    text-align: center;
    margin-top: 20px;
}

/* MEDIA QUERIES */
@media (min-width: 350px) {
  .page{
    padding: 20px 50px;
  }
}

@media (min-width: 530px) {
  .page{
    padding: 20px 100px;
  }
}

@media (min-width: 800px) {
  .page{
    padding: 20px;
  }

  .links-content{
    margin: 30px auto 50px auto;
    width: 40%;
  }
}


@media (min-width: 1000px){
  .page{
    height: 90vh;
  }
}
